import { browser_api } from '@app/browser_api';

import {
  LocalStorageData,
  type LocalStorageKeyType,
} from '@app/storage/local/types/sharedTypes';
import { handleStorageQuotaError } from './utils/handleStorageQuotaError';

export async function getLocalStorageData<Key extends LocalStorageKeyType>(
  keys?: Key | Key[],
): Promise<Partial<Pick<LocalStorageData, Key>>> {
  try {
    if (!keys) {
      return browser_api.storage.local.get() as Promise<LocalStorageData>;
    }

    return browser_api.storage.local.get(keys) as Promise<
      Pick<LocalStorageData, Key>
    >;
  } catch (error) {
    await handleStorageQuotaError(error);
    throw error;
  }
}

export async function setLocalStorageData(
  data: Partial<LocalStorageData>,
): Promise<void> {
  try {
    await browser_api.storage.local.set(data);
  } catch (error) {
    await handleStorageQuotaError(error);
    throw error;
  }
}

export async function deleteLocalStorageData(
  keys: LocalStorageKeyType | LocalStorageKeyType[],
): Promise<void> {
  await browser_api.storage.local.remove(keys);
}
