import {
  LocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/types/sharedTypes';
import { isSentryEligible } from '@app/sentry/utils/isSentryEligible';
import { browser_api } from '@app/browser_api';

export const getEligibility = async (): Promise<boolean> => {
  const {
    userId,
    metaApiData,
  }: Pick<
    LocalStorageData,
    typeof LocalStorageKey.UserId | typeof LocalStorageKey.MetaApiData
    //   getLocalStorageData -  .
  > = await browser_api.storage.local.get([
    LocalStorageKey.UserId,
    LocalStorageKey.MetaApiData,
  ]);
  return isSentryEligible({ userId, metaApiData });
};
