import {
  SidebarAdsMessageTypes,
  SidebarSizeChangedMessage,
} from '@content/scripts/sidebarAds/types';

export const isSidebarSizeChangedMessage = (
  data: unknown,
): data is SidebarSizeChangedMessage => {
  return (
    !!data &&
    typeof data === 'object' &&
    'type' in data &&
    data.type === SidebarAdsMessageTypes.SIDEBAR_SIZE_CHANGED &&
    'height' in data &&
    typeof data.height === 'number' &&
    'width' in data &&
    typeof data.width === 'number'
  );
};
