import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function getFeatureStoreContentScript() {
  const { featureStore } = await getLocalStorageData(
    LocalStorageKey.FeatureStore,
  );

  const { featuresListLocal } = await getLocalStorageData(
    LocalStorageKey.FeaturesListLocal,
  );

  if (!featureStore) {
    throw new Error('No feature store found');
  }

  return {
    ...featureStore,
    featuresList: {
      ...featureStore.featuresList,
      ...featuresListLocal,
    },
  };
}
