import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import {
  PARENT_ID,
  PREVIOUS_ELEMENT_SELECTOR,
  YOUBOOST_SIDEBAR_AD_ID,
} from '@content/scripts/sidebarAds/constants';
import { isSidebarSizeChangedMessage } from '@content/scripts/sidebarAds/utils';
import { browser_api } from '@app/browser_api';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { getRegionKey } from '@content/i18n/utils';
import { getFeatureStoreContentScript } from '@content/utils/getFeatureStoreContentScript';
import { Feature } from '@app/features/types/Feature';

let observer: MutationObserver | null = null;
let listenerAttached = false;

const createOverlay = (sidebarUrl: string) => {
  const overlay = document.createElement('div');
  overlay.id = YOUBOOST_SIDEBAR_AD_ID;
  overlay.style.padding = '12px 0 24px';
  overlay.style.borderBottom = '1px solid var(--yt-spec-10-percent-layer)';
  overlay.style.boxSizing = 'border-box';

  const iframe = document.createElement('iframe');
  iframe.src = sidebarUrl;
  iframe.style.display = 'block';
  iframe.style.width = '100%';
  iframe.style.height = '0';
  iframe.style.maxWidth = '150px';
  iframe.style.margin = '0 auto';

  overlay.appendChild(iframe);

  return overlay;
};

const attachMessageListener = () => {
  if (listenerAttached) return;
  listenerAttached = true;

  window.addEventListener('message', (event: MessageEvent) => {
    if (!isSidebarSizeChangedMessage(event.data)) return;

    const iframes = document.querySelectorAll<HTMLIFrameElement>(
      `#${YOUBOOST_SIDEBAR_AD_ID} iframe`,
    );

    iframes.forEach((iframe) => {
      if (event.source === iframe.contentWindow) {
        iframe.style.height = `${event.data.height}px`;
      }
    });
  });
};

async function main() {
  console.warn('sidebarAds loading...');
  try {
    const { featuresList } = await getFeatureStoreContentScript();

    if (!featuresList?.[Feature.SidebarAds]) {
      console.warn('[sidebarAds] Sidebar ads feature is disabled.');
      return;
    }

    const { metaApiData, hasPremium } = await getLocalStorageData([
      LocalStorageKey.MetaApiData,
      LocalStorageKey.HasPremium,
    ]);

    if (hasPremium) {
      console.error('[sidebarAds] hasPremium, cannot render sidebar.');
      return;
    }

    const parent = document.getElementById(PARENT_ID);

    if (!parent) {
      console.error(
        `[sidebarAds] Parent element with ID "${PARENT_ID}" not found, cannot insert sidebar.`,
      );
      return;
    }

    if (!metaApiData) {
      console.error(
        '[sidebarAds] metaApiData is missing, cannot render sidebar.',
      );
      return;
    }

    const { sidebarAdsUrl_v2, advMinVersion } = metaApiData;

    const key = await getRegionKey();
    const sidebarUrl = sidebarAdsUrl_v2?.[key];
    if (!sidebarUrl) {
      console.error(
        '[sidebarAds] sidebarUrl are missing, cannot render sidebar.',
      );
      return;
    }

    if (!advMinVersion) {
      console.error(
        '[sidebarAds] advMinVersion are missing, cannot render sidebar.',
      );
      return;
    }

    const currentVersion = browser_api.runtime.getManifest().version;
    const isShown =
      versionStringToNumber(currentVersion) <=
      versionStringToNumber(advMinVersion);

    if (!isShown) {
      console.error(
        '[sidebarAds] advMinVersion is lower than current version, cannot render sidebar.',
      );
      return;
    }

    if (observer) observer.disconnect();

    observer = new MutationObserver(async () => {
      const { featuresList } = await getFeatureStoreContentScript();
      const { hasPremium } = await getLocalStorageData(
        LocalStorageKey.HasPremium,
      );

      if (!featuresList?.[Feature.SidebarAds] || hasPremium) {
        return;
      }

      const firstTarget: HTMLElement | null = parent.querySelector(
        PREVIOUS_ELEMENT_SELECTOR,
      );

      const existingOverlay = document.getElementById(YOUBOOST_SIDEBAR_AD_ID);

      if (firstTarget && !existingOverlay) {
        const overlay = createOverlay(sidebarUrl);
        firstTarget.insertAdjacentElement('afterend', overlay);
      }

      if (!firstTarget && existingOverlay) {
        existingOverlay.remove();
      }
    });

    observer.observe(parent, { childList: true, subtree: true });

    attachMessageListener();

    const initialTarget = parent.querySelector(PREVIOUS_ELEMENT_SELECTOR);
    if (!initialTarget) return;

    const overlay = createOverlay(sidebarUrl);
    initialTarget.insertAdjacentElement('afterend', overlay);
    console.warn('sidebarAds loaded');
  } catch (error) {
    console.error('[sidebarAds]', error);
  }
}

void main();
