import { LocalStorageData } from '@app/storage/local/types/sharedTypes';
import { uniformBucket } from '@app/sentry/utils/uniformBucket';

type ArgsType = {
  userId: LocalStorageData['userId'];
  metaApiData: LocalStorageData['metaApiData'];
};

export function isSentryEligible({ userId, metaApiData }: ArgsType): boolean {
  if (typeof userId === 'undefined' || typeof metaApiData === 'undefined') {
    return false;
  }

  const sentrySampleRate = metaApiData?.sentrySampleRate ?? 0;
  const rate = Math.max(0, Math.min(1, sentrySampleRate));

  if (rate === 0) return false;

  const bucket = uniformBucket(userId); // 0..99
  const threshold = Math.floor(rate * 100); // 0..100
  return bucket < threshold;
}
