import { backgroundScope } from '@background/network/sentryScopeBackground';
import { LogsStorageKey } from '../types/logsTypes';
import { isQuotaError } from './isQuotaError';
import { browser_api } from '@app/browser_api';

export async function handleStorageQuotaError(error: unknown): Promise<void> {
  if (!isQuotaError(error)) {
    return;
  }

  backgroundScope.setTag('error_type', 'storage_quota_exceeded');
  backgroundScope.setTag('component', 'local_storage');
  backgroundScope.setLevel('error');
  backgroundScope.captureException(error);

  try {
    await browser_api.storage.local.remove(LogsStorageKey.Logs);
  } catch (nestedError) {
    console.error('storage:failed to remove logs', nestedError);
  }
}
