import { type SupportedLanguages } from '@app/i18n/types';
import { PRIORITY_LANGUAGE } from '@app/i18n/constants';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export const detectLanguage = async (): Promise<SupportedLanguages> => {
  try {
    const { currentLocale } = await getLocalStorageData(
      LocalStorageKey.CurrentLocale,
    );
    return currentLocale
      ? (currentLocale as SupportedLanguages)
      : PRIORITY_LANGUAGE;
  } catch {
    return PRIORITY_LANGUAGE;
  }
};
